import Vue from 'vue'
import Vuex from 'vuex'
import ls from '../utils/localStorage'
import router from '../router'
import * as moreActions from './action'
import * as moreGetters from './getters'

Vue.use(Vuex);

//   store.state.user
const state = {
    user: ls.getItem('user'),
    //
    auth: ls.getItem('auth'),
    articles: ls.getItem('articles'),
    search: '',
    origin: location.origin
};
//store.commit('UPDATE_USER'"user")
//
const mutations = {
    //UPDATE_USER
    UPDATE_USER(state, user) {
        state.user = user;
        ls.setItem('user', user)
    },
    UPDATE_AUTH(state, auth) {
        state.auth = auth;
        ls.setItem("auth", auth)
    },
    UPDATE_ARTICLES(state, articles) {
        state.articles = articles;
        ls.setItem('articles', articles)
    },
    UPDATE_SEARCH_VALUE(state, searchValue) {
        state.serchValue = searchValue;
    }
};
//actioncontextcontext.statestate
// context.commit{commit}
const actions = {
    login({commit}, user) {
        //
        if (user) {
            commit('UPDATE_USER', user)
        }
        commit('UPDATE_AUTH', true);
        router.push('/')
    },
    logout({commit}, user) {
        commit('UPDATE_AUTH', false);
        router.push({name: 'Home', params: {logout: true}})
    },
    //
    updateUser({state, commit}, user) {
        const stateUser = state.user;
        if (stateUser && typeof stateUser === 'object') {
            //  user = Object.assign({}, stateUser, user)
            user = {...stateUser, ...user};
            console.log(user);
            // Object.assign()
        }
        commit('UPDATE_USER', user);
    },
    //  moreActions
    //const actions = Object.assign(actions, moreActions)
    ...moreActions

}
//action
// const actions={
//     login(context,user){
//         if (user) context.commit('UPDATE_USER',user)
//         router.push("/")
//     }
// }
const getters = {
    getArticleById: (state, getters) => (id) => {
        //  computedArticles 
        let articles = getters.computedArticles;
        if (Array.isArray(articles)) {
            //idarticleId
            articles = articles.filter(article => parseInt(id) === parseInt(article.articleId));
            //  null
            return articles.length ? articles[0] : null;
        } else {
            return null;
        }
    },
    //  moreGetters,  getters = Object.assign(getters, moreGetters)
    ...moreGetters
};
const store = new Vuex.Store({
    getters,
    state,
    mutations,
    actions
});

export default store
