import Vue from 'vue'
import Router from 'vue-router'
import routes from './router'

Vue.use(Router)

const router = new Router({
    // mode 'hash'  # 
    //  'history'  History API 
    mode: 'history',
    //linkExactActiveClass<router-link></router-link>
    //<router-link>'router-link-exact-active'active
    // Bootstrap
    linkExactActiveClass: 'active',
    /**
     * scrollBehavior  history.pushState 
     * history.pushState 
     * @param to
     * @param from
     * @param savedPosition
     * @returns {{x: number, y: number}|{selector: *}|*}
     */
    // 
    scrollBehavior(to, from, savedPosition) {
        if (to.hash) {
            // 
            return {selector: to.hash}
        } else if (savedPosition) {
            // 
            return savedPosition
        } else {
            // 
            return {x: 0, y: 0}
        }
    },
    routes
});


router.beforeEach((to, from, next) => {
    const app = router.app;
    const store = app.$options.store;
    const auth = store.state.auth;
    //Id
    const articleId = to.params.articledId;
    //
    const user = store.state.user && store.state.user.name;
    // 
    const paramUser = to.params.user;
    app.$message.hide();

    if (
        (auth && to.path.indexOf('/auth/') !== -1) ||
        (!auth && to.meta.auth) ||
        //ID
        (articleId && !store.getters.getArticleById(articleId)) ||
        // 
        (paramUser && paramUser !== user && !store.getters.getArticlesByUid(null, paramUser).length)
    ) {
        next('/')
    } else {
        next()
    }
});
//
router.afterEach((to, from) => {
    const app = router.app;
    const store = app.$options.store;
    const showMsg = to.params.showMsg;
    if (showMsg) {
        if (typeof showMsg === 'String') {
            app.$message.show(showMsg)
        } else {
            app.$message.show('')
        }
    }
})

export default router
